<template>
  <div class="portfoilo">
    <section class="container list">
      <div class="text-center">
        <h1></h1>
      </div>
      <div class="row menu">
        <div class="col-sm-12 text-center">
          <button v-for="(item,index) in typeList" :key="index" @click="filterProject(index,item.code);"
                  type="button"
                  :disabled="!elContainer"
                  :class="['btn',index==actionIndex?'btn-default':'btn-info']" :data-filter="item.code">
            {{ item.name }}
          </button>
          <!--<button v-for="(item,index) in typeList" type="button" :class="['btn',{}]" :data-filter="item.code">{{item.name}}</button>-->
          <!--<button type="button" class="btn btn-default" data-filter="*"></button>-->
          <!--<button type="button" class="btn btn-info" data-filter=".webApp">APP</button>-->
          <!--<button type="button" class="btn btn-info" data-filter=".responsive"></button>-->
          <!--<button type="button" class="btn btn-info" data-filter=".html5">HTML5</button>-->
