          <!--<button type="button" class="btn btn-info" data-filter=".store"></button>-->
          <!--<button type="button" class="btn btn-info" data-filter=".game"></button>-->
        </div>
      </div>
      <div class="row" style="margin-left: 0;margin-right: 0;">
        <ul id="container-sort" class="project list-unstyled">
          <li v-for="(item,index) in portfolioList" :key="index" class="item col-md-3 col-sm-4 col-xs-6 thumb"
              :class="item.category">
            <a target="_blank" :href="item.href">
              <div class="thumb-wrap">
                <div class="response_square">
                  <div class="response_square_child">
                    <div class="over-hid rel">
                      <div class="mask trans"><span class="glyphicon glyphicon-link"></span></div>
                      <img class="img-responsive" :src="item.imgSrc"/>
                    </div>
                  </div>
                </div>
                <div class="describe text-center">{{ item.name }}</div>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>
<script type="text/ecmascript-6">
import Isotope from 'isotope-layout'
import { getPortfolio } from '@/services/portfolio.service.js'
import portfolioInfo from '~/public/simulate_data/portfolio.json'

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Portfolio',
  data () {
    return {
      scripts: [],
      actionIndex: 0,
      typeList: [],
      portfolioList: [],
      elContainer: null
    }
  },
  mounted: function () {
    this.getPortfolio()
  },
  methods: {
    initPkjd: function () {
      this.elContainer = new Isotope('#container-sort', {
        itemSelector: '.item',
        layoutMode: 'fitRows'
      })
    },
    async getPortfolio () {
      let data = {}
      try {
        const res = await getPortfolio()
        data = res.data
      } catch (e) {
        data = portfolioInfo.data
      }
      this.typeList = data.portfolioTypeList || []
      this.portfolioList = data.portfolioList || []
      this.$nextTick(function () {
        this.initPkjd()
      })
    },
    filterProject: function (i, attr) {
      this.actionIndex = i
      this.elContainer.arrange({ filter: attr })
    }
  }
}
</script>
